<template>
  <div class="weather-chart">
    <highcharts :options="chartOptions" />
    <div v-if="loading" class="weather-chart__preloader"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      chartOptions: {
        chart: {
          backgroundColor: '#6c879c',
          type: 'spline' // spline, line, column, pie, area
        },
        title: {
          text: '  8 ',
          style: {
            fontSize: 25,
            color: '#000',
            fontWeight: 'bold',
            textShadow: '2px 8px 10px rgba(0,0,0,.5)'
          }
        },
        subtitle: {
          text: ''
        },
        accessibility: {
          enabled: false //    
        },
        plotOptions: {
          series: {
            connectNulls: true,
            marker: {
              radius: 2
            }
          },
          column: {
            grouping: false,
            stacking: 'normal'
          }
        },
        tooltip: {
          crosshairs: true,
          shared: true,
          useHTML: false,
          formatter() {
            return /*html*/ `<strong>&nbsp;${this.points[0].key.day}</strong>
                            <br>
                            <strong> : ${
                              this.points[0].key.min > 0
                                ? '+' + this.points[0].key.min + ' '
                                : this.points[0].key.min + ' '
                            } / ${
              this.points[0].key.max > 0
                ? '+' + this.points[0].key.max + ' '
                : this.points[0].key.max + ' '
            }
                            <br>
                            ${this.points[0].key.conditions}
                            </strong>
                            `
          }
        },
        xAxis: {
          categories: [],
          labels: {
            style: {
              color: '#000',
              fontSize: 13,
              fontWeight: 'bold',
              textShadow: '2px 8px 10px rgba(0,0,0,.5)'
            },
            formatter() {
              return this.value.day
            }
          },
          gridLineWidth: 1,
          gridLineColor: '#7796ad'
        },
        yAxis: [
          {
            gridLineWidth: 1,
            gridLineColor: '#7796ad',
            labels: {
              style: {
                color: '#000',
                fontSize: 14,
                fontWeight: 'bold',
                textShadow: '2px 6px 10px rgba(0,0,0,.5)'
              },
              formatter() {
                return `${this.value > 0 ? `+${this.value}` : this.value} `
              }
            },
            type: 'linear', // 'linear', 'logarithmic', 'datetime'
            title: {
              text: ''
            }
          }
        ],
        legend: {
          enabled: false
        },
        series: [
          {
            data: [],
            color: '#00ffe1' // 00c4ff
          }
        ]
      },
      weatherConditions: {
        Clear: '',
        Clouds: '',
        Rain: '',
        Drizzle: '',
        Thunderstorm: '',
        Snow: '',
        Mist: '',
        Smoke: ''
      }
    }
  },
  methods: {
    async fetchWeather() {
      this.loading = true
      const url =
        'http://api.openweathermap.org/data/2.5/onecall?lat=55.7522&lon=37.6156&APPID=12594a2144aa9c83dfdc5b5b4f7c3b93'
      try {
        const responce = await fetch(url)
        const res = await responce.json()

        this.chartOptions.xAxis.categories = res.daily.map((el) => {
          return {
            day: new Date(el.dt * 1000).toLocaleDateString(),
            conditions: this.weatherConditions[el.weather[0].main],
            min: Math.round(el.temp.min - 273.15),
            max: Math.round(el.temp.max - 273.15)
          }
        })

        this.chartOptions.series[0].data = res.daily.map((el) => {
          return {
            y: Math.round(el.temp.day - 273.15),
            marker: {
              symbol: `url(${require(`@/assets/img/${el.weather[0].icon}.png`)})`,
              width: 60,
              height: 45
            }
          }
        })
      } catch (err) {
        console.log(err)
      } finally {
        this.loading = false
      }
    }
  },
  created() {
    this.fetchWeather()
  }
}
</script>

<style>
.weather-chart {
  position: relative;
}

.weather-chart .highcharts-credits {
  display: none;
}

.weather-chart__preloader {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background: rgba(255, 255, 255, 0.7); */
}

.weather-chart__preloader:after {
  content: '';
  width: 30px;
  height: 30px;
  border: 4px solid #0863ef;
  border-radius: 50%;
  border-right-color: transparent;
  box-sizing: border-box;
  animation: rotatePreloader 0.5s linear infinite;
  color: #6c879c;
}

@keyframes rotatePreloader {
  100% {
    transform: rotateZ(360deg);
  }
}
</style>
